* {
  box-sizing: border-box;
  outline: none;
  font-family: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Helvetica Neue", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Meiryo UI", Arial, sans-serif;
  overflow-x: hidden; }

input::-ms-clear {
  display: none; }

a {
  color: inherit;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

input {
  font-size: 16px;
  height: 40px;
  border: none;
  padding: 0 10px;
  background-color: white; }

textarea {
  font-size: 16px;
  border: none;
  padding: 10px;
  background-color: white; }

button {
  font-size: inherit;
  transition: 0.2s;
  cursor: pointer;
  border: none;
  font-size: 14px; }
